/* NAVIGATION */
nav {
  background: transparent;
  height: 80px;
  overflow: hidden;
  display: flex;
  align-content: space-between;
  justify-content: center;
}
nav .nav-ul {
  list-style: none;
  /* position: absolute;
  margin-bottom: 0px;
  margin-left: 850px; */
  display: flex;
  align-content: space-between;
  padding-left: 45%;
  padding-top: 15px;
}

nav .nav-ul .nav-list {
  display: inline-block;
  margin: 12px;
  margin-top: 20px;
}
nav .nav-ul .nav-list .nav-index {
  display: block;
  position: relative;
  text-decoration: none;
  color: rgb(247, 245, 245);
  font-weight: 500;
  font-size: 18px;
  padding: 5px;
  font-family: "Alata", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/* #home {
  color: rgb(216, 138, 74);
} */

nav .nav-ul .nav-list .nav-index,
nav .nav-ul .nav-list .nav-index:after,
nav .nav-ul .nav-list .nav-index:before {
  transition: all 0.5s;
}
nav .nav-ul .nav-list .nav-index:hover {
  color: #555;
}

/* stroke */
nav.stroke .nav-ul .nav-list .nav-index,
nav.navbar .nav-ul .nav-list .nav-index {
  position: relative;
}
nav.stroke .nav-ul .nav-list .nav-index:after,
nav.navbar .nav-ul .nav-list .nav-index:after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background: rgb(253, 250, 250);
  height: 1px;
}
nav.stroke .nav-ul .nav-list .nav-index:hover:after {
  width: 100%;
}

nav.navbar .nav-ul .nav-list .nav-index {
  transition: all 2s;
}

nav.navbar .nav-ul .nav-list .nav-index:after {
  text-align: left;
  content: ".";
  margin: 0;
  opacity: 0;
}
nav.navbar .nav-ul .nav-list .nav-index:hover {
  color: rgb(0, 0, 0);
  z-index: 1;
}
nav.navbar .nav-ul .nav-list .nav-index:hover:after {
  z-index: -10;
  animation: navbar 1s forwards;
  -webkit-animation: navbar 1s forwards;
  -moz-animation: navbar 1s forwards;
  opacity: 1;
}

/* Keyframes */
@-webkit-keyframes navbar {
  0% {
    width: 0%;
    height: 1px;
  }
  50% {
    width: 100%;
    height: 1px;
  }
  100% {
    width: 100%;
    height: 120%;
    bottom: -10%;

    background: rgb(255, 255, 255);
  }
}
@keyframes navbar {
  0% {
    width: 0%;
    height: 1px;
  }
  50% {
    width: 100%;
    height: 1px;
  }
  100% {
    width: 100%;
    height: 120%;
    bottom: -10%;

    background: rgb(255, 255, 255);
  }
}

